import React, { useState } from "react";
import { Layout, Row, Col, Button, Input, Divider, Spin } from "antd";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import InfoPopover from "../table/InfoPopover";
import { Link, useHistory } from "react-router-dom";
import { CREATE, INFO } from "../../headings";
const { Content } = Layout;

const { Search } = Input;

const Info = (props) => {
  const history = useHistory();
  const [search, setSearch] = useState("");
  const loader = props.loader;
  const handleOnSearch = () => {
    props.setSearchValue(search);
  };

  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={props.data} />
              <Content>
                <Row className="py-3 justify-content-between">
                  <h1 className="mb-0">{INFO}</h1>
                </Row>

                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                      <Row>
                        <Col span={24} lg={9} className="search-box">
                          <Search
                            onChange={(e) => {
                              setSearch(e.target.value);
                            }}
                            onBlur={handleOnSearch}
                            placeholder="Search"
                          />
                        </Col>
                        <Col
                          span={24}
                          lg={6}
                          className="ml-0 ml-lg-4 mt-2 mt-lg-0"
                        ></Col>
                      </Row>
                    </Col>

                    <Col
                      span={24}
                      lg={6}
                      className="d-flex align-items-center justify-content-end"
                    >
                      <Button
                        onClick={() => {
                          history.push("/orgcc/createinfo");
                        }}
                        type="primary"
                      >
                        {CREATE}
                      </Button>
                    </Col>
                  </Row>
                </Col>
                <div className="mid-content">
                  <Spin spinning={loader} size="large">
                    {props.infos.map((value) => {
                      return (
                        <Row key={value.info_id}>
                          <Col span={22} className="editor-data">
                            <h6 className="font-weight-md6 mb-1">
                              <Link
                                to={{
                                  pathname: `/orgcc/infodetails`,
                                  state: value,
                                }}
                              >
                                {value.title}
                              </Link>
                            </h6>
                          </Col>
                          <Col span={2} className="d-flex justify-content-end">
                            <InfoPopover data={value} />
                          </Col>
                          <Divider className="w-100" />
                        </Row>
                      );
                    })}
                  </Spin>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default Info;
